/**=====================
	4.1. Dark CSS Start
==========================**/
body {
	&.dark-only {
		color: $dark-all-font-color;
		background-color: $dark-body-background;

		.form-control {
			background-color: $dark-card-background  !important;
			border-color: $dark-card-border  !important;
			color: rgba($white, 0.4);
		}

		.filepond--drop-label {
			background-color: $dark-body-background;
		}

		.email-wrap {
			.flex-grow-1 {
				>h4 {
					color: rgba($white, 0.8);

					small {
						span {
							color: rgba($white, 0.2);
						}
					}
				}
			}
		}

		.progress {
			background-color: $dark-body-background;
		}

		.tagify[disabled] {
			background-color: $dark-body-background;
		}

		.international-num input {
			background-color: $dark-body-background;
		}

		#row_create {
			td {
				&.highlight {
					background-color: $dark-body-background  !important;
				}
			}
		}

		.dropdown-menu {
			background-color: $dark-card-background  !important;

			.dropdown-item {
				background-color: $dark-card-background  !important;
				color: $white;
			}
		}

		.timeline-list {
			&.timeline-border {
				border-bottom: 1px solid $dark-body-background;
			}
		}

		.dropdown-basic {
			.dropdown {
				.dropdown-content {
					background-color: $dark-card-background  !important;

					a {
						background-color: $dark-card-background  !important;
						color: $white;
					}
				}
			}
		}

		.icon-lists {
			div {
				&:hover {
					background-color: $dark-body-background;
				}
			}
		}

		.flot-chart-container {
			.morris-hover {
				&.morris-default-style {
					background: $dark-body-background;
					color: rgba($white, 0.7);
				}
			}
		}

		.blockquote {
			&.light-card {
				background-color: $dark-body-background;
			}
		}

		.radius-wrapper {
			background-color: $dark-card-background;
		}

		.apache-cotainer-large,
		.apache-cotainer,
		.test-chart {
			div {
				&:nth-child(2) {
					background-color: $dark-body-background  !important;
					color: rgba($white, 0.8) !important;
				}
			}
		}

		.page-wrapper {
			&.compact-sidebar {
				.page-body-wrapper {
					.sidebar-wrapper {
						div {
							nav.sidebar-main {
								background-color: $dark-card-background  !important;
							}
						}
					}
				}

			}
		}

		.touchspin-wrapper {
			input[type='number'] {
				color: $white;
			}
		}

		.pre-post-touchspin {
			.input-touchspin {
				color: $white;
				border: 1px solid $dark-body-background;
			}
		}

		.basic-wizard {
			.stepper-horizontal {
				.step {
					.step-circle {
						background: $dark-body-background;
					}

					.editing {
						.step-circle {
							background-color: $dark-body-background;
							border-color: 1pc solid rgba($white, 0.05);
						}
					}
				}
			}
		}

		.horizontal-wizard-wrapper {
			.main-horizontal-header {
				.horizontal-options {
					a {
						.horizontal-wizard {
							color: rgba($white, 0.8);

							.stroke-icon-wizard {
								i {
									color: rgba($white, 0.8);
								}
							}
						}
					}

					.nav-link {
						&.active {
							background-color: unset;

							.horizontal-wizard {
								color: $primary-color;
							}
						}
					}
				}
			}
		}

		.vertical-main-wizard {
			.header-vertical-wizard {
				background-color: $dark-body-background;

				.nav-link {
					border-bottom: 1px solid rgba($white, 0.05);
				}
			}

			.nav-link {
				.vertical-wizard {
					.vertical-wizard-content {
						h6 {
							color: rgba($white, 0.7);
						}
					}
				}
			}
		}

		.main-inline-calender {
			&.input-group {
				.form-control {
					color: rgba($white, 0.9) !important;
				}

				.flatpickr-calendar {
					background-color: $dark-body-background  !important;
					border: none;
				}

				.flatpickr-rContainer {
					.flatpickr-weekdays {
						.flatpickr-weekdaycontainer {
							color: $white  !important;
						}
					}

					.flatpickr-days {
						.dayContainer {
							.flatpickr-day {
								color: rgba($white, 0.4);

								&:hover {
									color: rgba($white, 0.7);
									background-color: $dark-body-background;
								}
							}
						}
					}
				}
			}
		}

		.default-dashboard {
			.dropdown {
				.dropdown-toggle {
					background-color: $dark-body-background  !important;
				}
			}

			.products-card {
				.d-flex {
					.flex-grow-1 {
						.progress {
							background-color: rgba($dark-body-background, 0.8) !important;
						}
					}

					.flex-shrink-0 {
						background-color: rgba($dark-body-background, 0.8) !important;

						img {
							filter: brightness(0.5) invert(1);
						}
					}
				}

				.user-box {
					background-color: rgba($white, 0.2) !important;

					i {
						color: $white  !important;
					}
				}
			}

			.wadewarren {
				h3 {
					color: rgba($white, 0.9);
				}

				.img-overlay {
					@media (max-width: 767px) {
						background-color: rgb(54 59 48 / 80%);

						p {
							color: rgba($white, 0.9);
						}
					}
				}
			}

			#weekly-visitors {
				.apexcharts-radar-series {
					polygon {
						&:nth-child(odd) {
							fill: $dark-body-background  !important;
						}

						&:nth-child(even) {
							fill: $dark-card-background  !important;
						}
					}
				}

				.apexcharts-series {
					path {
						fill: rgba($white, 0.7);
					}
				}
			}

			.profile-greeting {
				background-image: radial-gradient(rgba(193, 233, 193, 10%), rgba(193, 233, 193, 10%));
			}

			.sellingproduct {
				table {
					tbody {
						tr {
							td {

								.delete-option,
								.edit-option {
									background-color: $dark-body-background;

									a {
										svg {
											stroke: rgba($white, 0.7);
										}
									}
								}
							}
						}
					}
				}
			}

			.order-box {
				border: 1px solid $dark-body-background;

				.d-flex {
					.flex-grow-1 {
						h3 {
							color: rgba($white, 0.8);
						}
					}
				}
			}

			.track-order {
				li {
					&:after {
						background-color: $dark-body-background;
					}

					.d-flex {
						.flex-shrink-0 {
							background-color: $dark-body-background;
						}

						.flex-grow-1 {
							border-bottom: 1px solid $dark-body-background;

							h5 {
								color: rgba($white, 0.8);
							}
						}
					}

					&:last-child {
						.d-flex {
							.flex-grow-1 {
								padding-bottom: 0px;
								border-bottom: none;
							}
						}
					}
				}
			}

			div.dataTables_wrapper {
				table.dataTable {
					border: none !important;

					&.display {
						tbody {
							tr.odd {
								background-color: transparent !important;

								>.sorting_1 {
									background-color: transparent !important;
								}
							}
						}
					}
				}
			}

			table {
				tbody {
					tr {
						td {
							h5 {
								color: rgba($white, 0.7);
							}

							P {
								color: rgba($white, 0.7);
							}

							.d-flex {
								.flex-grow-1.ms-2 {
									h5 {
										color: rgba($white, 0.7);
									}
								}
							}

							.order {
								background-color: $dark-body-background;
								color: rgba($white, 0.6);
							}
						}
					}
				}
			}

			.balance-card {
				hr {
					border: 1px dashed $theme-body-sub-title-color;
				}

				li {
					h3 {
						color: $white;
					}

					span {
						color: rgba($white, 0.8);
					}
				}
			}

			.sales-summary {
				p {
					color: rgba($white, 0.7);
				}
			}
		}

		.ecommerce-dashboard {
			.earning {
				p {
					strong {
						color: rgba($white, 0.7);
					}
				}
			}
		}

		.search-page {
			.info-block {
				border: 1px solid $dark-border-color;
				background-color: $dark-body-background;

				a {
					color: rgba($white , 0.7);
				}
			}
			 ul{
				&.search-info {
					li{
						color: rgba($white , 0.7);
					}
				}
			 }
		}

		.pricingtable {
			background-color: $dark-body-background;
		}

		.checkbox-checked {
			.sub-title {
				border-bottom: 1px solid transparent !important;
			}
		}

		.email-app-sidebar {
			.main-menu {
				>li {
					a {
						color: $white;

						&:hover {
							background-color: $dark-body-background;
						}
					}
				}
			}
		}

		.ecommerce-dashboard {
			.dropdown {
				.dropdown-toggle {
					background-color: $dark-body-background  !important;
				}
			}
		}

		.email-app-sidebar {
			.d-flex img {
				border: 2px solid rgba($white, 0.04);
			}

			.main-menu {
				>li {
					a {
						color: $white;
					}
				}
			}

			ul li {
				a {
					> {
						.title {
							color: $white  !important;
						}
					}
				}

			}
		}

		.email-wrap {
			.email-right-aside {
				.email-body {
					.inbox {
						.d-flex {
							&.active {
								background-color: $dark-body-background  !important;
							}
						}
					}
				}
			}
		}

		.email-app-sidebar {
			ul li {
				a {
					> {
						.title {
							color: rgba($dark-body-background, 0.8);
						}
					}
				}
			}
		}

		.schedule-lesson {
			.datepicker--nav-title {
				background-color: $header-dark;
			}

			.datepicker--nav-action {
				background: transparent;

				path {
					stroke: $white;
				}
			}
		}

		.ecommerce-dashboard {
			div.dataTables_wrapper {
				table.dataTable {
					border: none !important;

					&.display {
						tbody {
							tr.odd {
								background-color: transparent !important;

								>.sorting_1 {
									background-color: transparent !important;
								}
							}
						}
					}
				}
			}
		}

		.dropdown {
			.dropdown-toggle {
				background-color: transparent;
				color: rgba($white, 0.6);
			}
		}

		.transaction-card,
		.userinformation,
		.manageorder {
			table {
				tbody {
					tr {
						td {
							h5 {
								color: rgba($white, 0.7);
							}

							P {
								color: rgba($white, 0.7);
							}

							.d-flex {
								.flex-shrink-0 {
									background-color: $dark-body-background;
								}

								.flex-grow-1.ms-2 {
									h5 {
										color: rgba($white, 0.7);
									}
								}
							}

							.order {
								background-color: $dark-body-background;
								color: rgba($white, 0.6);
							}
						}
					}
				}
			}
		}

		.over-rating {
			>p {
				border-bottom: 1px solid $dark-body-background;
			}

			.over-all {
				h3 {
					color: rgba($white, 0.7);
				}
			}

			.rating-box {
				border-left: 1px dashed $dark-body-background;

				.d-flex {
					border-bottom: 1px dashed $dark-body-background;

					&:last-child {
						border-bottom: none;
					}
				}
			}
		}

		.message-box {
			li {
				.d-flex {
					.flex-grow-1 {
						a {
							h5 {
								color: $white  !important;
							}
						}
					}

					span {
						background-color: $dark-body-background;
						color: rgba($white, 0.7);
					}
				}
			}
		}

		.traffic-list {
			li {
				border-color: $dark-body-background;

				div {
					h5 {
						color: rgba($white, 0.8);
					}

					span {
						span {
							strong {
								color: rgba($white, 0.7);
							}
						}
					}
				}
			}
		}

		.news-box {
			li {
				border-bottom: 1px solid $dark-body-background;

				&:last-child {
					border-bottom: none;
				}

				.d-flex {
					.flex-grow-1 {
						h5 {
							color: rgba($white, 0.8);
						}

						p {
							color: $card-subheading-color;
						}
					}

					span {
						background-color: $dark-body-background;
						color: rgba($white, 0.6);
					}
				}
			}
		}

		.tagify {
			--tags-focus-border-color: $header-dark;
			--tags-border-color: $header-dark;

			.tagify__tag {
				div {
					background-color: $white;
				}
			}
		}

		.tagify__tag {
			>div {
				background-color: $primary-color;
			}
		}

		.card-wrapper {
			.form-control {
				color: rgba($white, 0.2);
			}
		}

		.checkbox-wrapper,
		.radio-wrapper {
			li {
				input {
					background-color: transparent;
				}
			}
		}

		&.light,
		&.box-layout {
			.page-sub-header {
				.body-header {
					.sidebar-body-menu {
						>li {
							a {
								svg {
									stroke: rgba($white, 0.6);
								}
							}

							.sidebar-body-mainmenu {
								background-color: $dark-card-background;

								>li {
									ul.sidebar-body-submenu {
										background-color: $dark-card-background;
									}
								}
							}
						}
					}
				}
			}

			// Select2 
			.tagify__dropdown__item--active {
				background-color: $dark-body-background;
			}

			.tagify__input {
				color: $dark-small-font-color;

				&::before {
					color: $dark-small-font-color;
				}
			}

			.tagify {
				border: 1px solid $header-dark  !important;
				--tags-focus-border-color: $header-dark;
			}

			.tagify--select {
				border: 1px solid rgba($white, 0.2) !important;
			}

			.page-wrapper {
				&.material-icon {
					.page-body-wrapper {
						.page-title {
							.breadcrumb {
								.breadcrumb-item {
									color: rgba($white, 0.75);

									&.active {
										color: $white;
									}

									&:before {
										color: rgba($white, 0.6);
									}
								}
							}
						}
					}
				}
			}
		}

		.page-body {
			.bg-light {
				background-color: $dark-card-background  !important;
			}
		}

		.lang {
			span.lang-txt {
				color: $white;
			}

			span {
				span {
					color: #999;
				}
			}
		}

		.table-striped {
			tbody {
				tr {
					&:nth-of-type(odd) {
						background-color: $dark-card-background;
						--bs-table-accent-bg: unset;
						color: rgba($white, 0.9);
					}
				}
			}
		}

		.page-wrapper {
			.page-header {
				.header-wrapper {
					.search-full {
						border: 1px solid $dark-card-border;

						.form-group {
							input {
								background-color: $dark-card-background;
							}
						}
					}
				}
			}

			&.material-icon {
				.page-header {
					.header-wrapper {
						.search-full {
							border: 1px solid $dark-card-border;

							.form-group {
								input {
									background-color: rgba($white, 0.1);
								}
							}
						}
					}
				}
			}
		}

		.page-sub-header {
			background-color: $dark-sub-header;

			.body-header {
				.sidebar-body-menu {
					>li {
						h3 {
							color: $white;
						}

						&:last-child {
							div {
								border-right: unset;
							}
						}
					}
				}
			}
		}

		.wizard-4 {
			.step-container {
				background-color: $dark-card-background;
				border-color: $dark-card-border;
			}

			.login-card {
				.login-main {
					background-color: $dark-body-background;
				}
			}
		}

		.icon-state {
			.switch-state {
				&:after {
					color: $dark-all-font-color;
				}
			}
		}

		.social-widget {
			.social-icons {
				box-shadow: 0px 42.1092px 27.8569px $dark-body-background, 0px 8.90772px 7.12618px $dark-body-background, 0px 2.02448px 3.44162px $dark-body-background;
				background: $dark-card-background;
			}

		}

		.calendar-basic {
			.md-sidebar-aside {
				background: $dark-card-background;
			}

			#external-events {
				border-color: $dark-card-border;
			}

			.calendar-default {
				.fc {

					.fc-daygrid-day-number,
					.fc-col-header-cell-cushion {
						color: $dark-all-font-color;
					}
				}
			}
		}

		.clockpicker-popover {
			.popover-title {
				background-color: $dark-card-background;
			}

			&.bottom {
				>.arrow {
					border-bottom-color: $dark-body-background;

					&:after {
						border-bottom-color: $dark-card-background;
					}
				}
			}

			&.top {
				>.arrow {
					border-top-color: $dark-body-background;

					&::after {
						border-top-color: $dark-card-background;
					}
				}
			}

			&.left {
				>.arrow {
					border-left-color: $dark-body-background;

					&::after {
						border-left-color: $dark-card-background;
					}
				}
			}

			.popover-title {
				border-bottom-color: $dark-card-background;
			}
		}

		.fc-theme-standard {

			.fc-scrollgrid,
			.fc-list {
				border-color: $dark-card-border;
			}

			.fc-list-day-cushion {
				background-color: $dark-body-background;
			}
		}

		.fc {
			.fc-list-event {
				&:hover {
					td {
						background-color: $dark-body-background;
					}
				}
			}
		}

		.apexcharts-canvas {
			.apexcharts-tooltip {
				&.apexcharts-theme-light {
					background: $dark-body-background  !important;
					color: $white;
					border: 1px solid rgba($white, 0.05) !important;
				}
			}
		}

		.view-container {
			.apexcharts-canvas {
				.apexcharts-point-annotations {
					circle {
						outline: 25px solid $light-primary;
					}
				}
			}
		}

		// crypto dashboard
		.currency-widget {
			div[class*=bg-light-] {
				color: $dark-all-font-color;
			}
		}

		.btn {
			&.badge-light-primary {
				&:hover {
					color: rgba($white, 0.6);
				}
			}
		}

		.portfolio-chart-container {
			.apexcharts-canvas {
				.apexcharts-radialbar {
					image {
						display: none;
					}
				}
			}
		}

		// online course dashboard css start
		svg.f-light {
			fill: rgba($white, 0.6);
			opacity: 1;
		}

		.course-widget {
			.btn-light {
				background-color: $dark-body-background  !important;
			}
		}

		.schedule-list {
			>li {
				box-shadow: 4px 7px 17px 5px rgba($dark-body-background, 0.2);
			}
		}

		.upcoming-event-wrap {
			.apexcharts-canvas {
				.apexcharts-data-labels {
					rect {
						fill: none;
						stroke: none;
					}
				}
			}
		}

		// online course dashboard  css end
		.page-wrapper {
			&.advance-layout {
				.page-header {
					background: $dark-card-background;
				}

				.page-body-wrapper {
					.page-title {
						background-color: transparent;
						border-bottom: none;
						box-shadow: none;
