  /**=====================
     14. Chat CSS Start
==========================**/
  .chat-box {
    .toogle-bar {
      display: none;
    }

    .chat-menu {
      max-width: 340px;
    }

    .people-list {
      .search {
        position: relative;

        .form-control {
          background-color: $light-body-bg-color;
          border: 1px solid $light-color;

          &::placeholder {
            color: $semi-dark;
          }
        }

        i {
          position: absolute;
          right: 10px;
          top: 17px;
          font-size: 14px;
          color: rgba($black, 0.2);

          [class="dark-only"] & {
            color: rgba($white, 0.5);
          }
        }
      }

      ul {
        padding: 0;

        li {
          padding-bottom: 20px;

          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }

    .user-image {
      float: left;
      width: 52px;
      height: 52px;
      margin-right: 5px;
    }

    .about {
      float: left;
      margin-top: 5px;
      padding-left: 10px;

      .name {
        color: $dark-color;
        letter-spacing: 1px;
        font-weight: 700;
      }
    }

    .status {
      color: $semi-dark;
      letter-spacing: 1px;
      font-size: 12px;
      margin-top: 5px;

      .chat-status {
        font-weight: 600;
        color: $theme-body-font-color;
      }

      p {
        font-size: 14px;
      }
    }

    .chat-right-aside {
      .chat {
        .chat-header {
          padding: 15px;
          border-bottom: 1px solid $light-color;
          display: flex;
          align-items: center;
          justify-content: space-between;

          img {
            float: left;
            width: 50px;
            height: 50px;
            box-shadow: 1px 1px 4px 1px $light-gray;
          }

          .chat-menu-icons {
            li {
              margin-right: 24px;

              a i {
                color: $theme-body-sub-title-color;
                font-size: 25px;
                cursor: pointer;
              }
            }
          }

          .chat-menu-icons {
            margin-left: auto;

            [dir="rtl"] & {
              margin-right: auto;
              margin-left: unset;
            }
          }
        }

        .chat-msg-box {
          padding: 20px;
          overflow-y: auto;
          height: 560px;
          margin-bottom: 80px;

          .chat-user-img {
            margin-top: -35px;
          }

          .message-data {
            margin-bottom: 10px;
          }

          .message-data-time {
            letter-spacing: 1px;
            font-size: 12px;
            color: $semi-dark;
          }

          .message {
            color: $dark-color;
            padding: 20px;
            line-height: 1.9;
            letter-spacing: 1px;
            font-size: 14px;
            margin-bottom: 30px;
            width: 50%;
            position: relative;
          }

          .my-message {
            border: 1px solid rgba($black, 0.1);
            border-radius: 10px;
            border-top-left-radius: 0;
          }

          .other-message {
            background-color: #c6c9cc33;
            border-radius: 10px;
            border-top-right-radius: 0;
          }
        }

        .chat-message {
          padding: 20px;
          border-top: 1px solid $light-body-bg-color;
          position: absolute;
          width: calc(100% - 15px);
          background-color: $white;
          border-radius: 0 0 0 10px;
          bottom: 0;

          .smiley-box {
            background: $light-semi-gray;
            padding: 10px;
            display: block;
            border-radius: 4px;
            margin-right: 0.5rem;
          }

          .text-box {
            position: relative;

            .input-txt-bx {
              height: 50px;
              border: 2px solid var(--theme-default);
              padding-left: 18px;
              font-size: 12px;
              letter-spacing: 1px;
            }

            i {
              position: absolute;
              right: 20px;
              top: 20px;
              font-size: 20px;
              color: $light-gray;
              cursor: pointer;
            }

            .btn {
              font-size: 16px;
              font-weight: 500;
              padding: 0.74rem 1.75rem;
              border-radius: 0 5px 5px 0;
            }
          }
        }
      }
    }

    .chat-menu {
      border-left: 1px solid $light-gray;

      .tab-pane {
        padding: 0 15px;
      }

      ul {
        li {
          .about {
            .status {
              i {
                font-size: 10px;
              }
            }
          }
        }
      }

      .user-profile {
        margin-top: 30px;

        .user-content {
          h5 {
            margin: 15px 0;
          }

          hr {
            margin: 15px 0;
          }

          p {
            font-size: 16px;
          }
        }

        .border-tab.nav-tabs .nav-item .nav-link.active,
        .border-tab.nav-tabs .nav-item .nav-link.show,
        .border-tab.nav-tabs .nav-item .nav-link:focus {
          border-bottom: 0;
        }

        .image {
          position: relative;

          .icon-wrapper {
            position: absolute;
            bottom: 0;
            left: 55%;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 35px;
            width: 35px;
            border-radius: 50%;
            background-color: $white;
            cursor: pointer;
            overflow: hidden;
            margin: 0 auto;
            font-size: 14px;
            box-shadow: 1px 1px 3px 1px $light-color;
          }

          .avatar img {
            border-radius: 50%;
            border: 5px solid $light-color;
          }
        }

        .border-right {
          border-right: 1px solid #dee2e6;
        }

        .follow {
          margin-top: 0;

          .follow-num {
            font-size: 20px;
            color: $black;
          }

          span {
            color: $theme-font-color;
            font-size: 14px;
            letter-spacing: 1px;
          }
        }

        .social-media a {
          color: $semi-dark;
          font-size: 15px;
          padding: 0 7px;
        }

        .chat-profile-contact {
          p {
            font-size: 14px;
            color: $semi-dark;
          }
        }
      }

      .nav {
        margin-bottom: 20px;
      }

      .nav-tabs .nav-item {
        width: 33.33%;

        a {
          padding: 15px !important;
          color: $semi-dark  !important;
          letter-spacing: 1px;
          font-size: 14px;
          font-weight: 700;
          height: 60px;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        .material-border {
          border-width: 1px;
          border-color: var(--theme-default);
        }

        .nav-link.active {
          color: $black  !important;
        }
      }
    }

    .chat-history {
      .call-content {
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 300px;
      }

      .total-time h2 {
        font-size: 50px;
        color: $light-semi-gray;
        font-weight: 600;
        margin-bottom: 30px;

        [class="dark-only"] & {
          color: rgba($white, 0.6);
        }
      }

      .receiver-img {
        margin-top: 55px;

        img {
          border-radius: 5px;
        }
      }

      .call-icons {
        margin-bottom: 35px;

        ul {
          li {
            width: 60px;
            height: 60px;
            border: 1px solid $light-color;
            border-radius: 50%;
            padding: 12px;
            cursor: pointer;

            +li {
              margin-left: 10px;
            }

            a {
              color: #999;
              font-size: 25px;
            }
          }
        }
      }
    }
  }

  .chat-left-aside {
    >.d-flex {
      margin-bottom: 15px;
    }

    .people-list {
      height: 625px;
    }

    ul li {
      position: relative;
    }
  }

  .status-circle {
    width: 12px;
    height: 12px;
    position: absolute;
    top: 4px;
    left: 44px;
    border-radius: 50%;
    border: 2px solid $white;
  }

  .away {
    background-color: $warning-color;
  }

  .online {
    background-color: $success-color;
  }

  .offline {
    background-color: $danger-color;
  }

  .chat-container {
    .aside-chat-left {
      width: 320px;
    }

    .chat-right-aside {
      width: 320px;
    }
  }

  .call-chat-sidebar {
    max-width: 320px;
  }

  .call-chat-sidebar,
  .chat-body {
    .card {
      .card-body {
        padding: 15px;
      }
    }
  }

  /**=====================
    14.  Chat CSS Ends
==========================**/