/**=====================
    3.20 Helper CSS start
==========================**/
.border-wrapper {
    padding: 20px;
    [class="dark-only"] & {
        background-color: $dark-body-background;
    }
    div{
        span{
            color: $black;
        }
    }
    h4{
        color: $header-dark;
        font-weight: 700;
        [class="dark-only"] & {
            color: rgba( $white , 0.6);
        }
    }
}
.helper-box {
    width: 40px;
    height: 40px;
    border-radius: 25%;
    &.helper-text {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 900;
    }
}
.helper-radius {
    width: 60px;
    height: 30px;
}
.fill-wrapper {
    background-color: $light-color;
}
.gradient-border {
    display: flex;
    gap: 22px;
    flex-wrap: wrap;
    align-items: center;
    @media (max-width: 1200px) {
        gap: calc(13px + (22 - 13) * ((100vw - 320px) / (1200 - 320)));
    }
}
.radius-wrapper {
    background-color: $light-color;
}
.alert-light-light{
    background-color: $light-color;
}
.font-wrapper {
    display: flex;
    align-items: center;
    padding: 12px 20px;
}
/**=====================
    3.20 Helper CSS end
==========================**/